//   - 6   
export const triplets = [
  'akk', 'kka', 'aka', 'kok', 'oko', 'koo'
];

//   - 3   
export const trainingTriplets = [
  'akk', 'kka', 'aka'
];

//    
export const getRandomTriplets = (count: number): string[] => {
  const shuffled = [...triplets].sort(() => 0.5 - Math.random());
  return shuffled.slice(0, count);
};
